import React from 'react';
import { Link } from 'gatsby';

import Layout from '../components/layout';
import SEO from '../components/seo';
import { useStaticQuery, graphql } from 'gatsby';
import Container from 'react-bootstrap/Container';

const ShelfPage = () => {
  const query = useStaticQuery(graphql`
    query {
      books: allAirtable(
        filter: {
          table: { eq: "Shelf" }
          data: {
            Stage: { eq: "Completed" }
            Rating: { gte: 4 }
            Medium: { eq: "Book" }
          }
        }
        sort: { fields: id, order: DESC }
      ) {
        edges {
          node {
            data {
              Title
              Artist
            }
          }
        }
      }
      music: allAirtable(
        filter: {
          table: { eq: "Shelf" }
          data: {
            Stage: { eq: "Completed" }
            Rating: { gte: 3 }
            Medium: { eq: "Music" }
          }
        }
        sort: { fields: id, order: DESC }
      ) {
        edges {
          node {
            data {
              Title
              Artist
            }
          }
        }
      }
      films: allAirtable(
        filter: {
          table: { eq: "Shelf" }
          data: {
            Stage: { eq: "Completed" }
            Rating: { gte: 4 }
            Medium: { eq: "Film" }
          }
        }
        sort: { fields: id, order: DESC }
      ) {
        edges {
          node {
            data {
              Title
              Artist
            }
          }
        }
      }
    }
  `);

  const { books, music, films } = getAirtableRows(query);

  return (
    <Layout>
      <SEO title="Shelf" />
      <Container className="intro-container shelf" fluid>
        <h1 className="title">Shelf</h1>
        <p>
          An unordered collection of books, music, films, and other works of art
          that I would recommend to others. Somewhat inspired by Patrick
          Collision's{' '}
          <a href="https://patrickcollison.com/bookshelf">bookshelf</a>.
        </p>
        {renderNameAndList('Books:', books)}
        {renderNameAndList('Music:', music)}
        {renderNameAndList('Films:', films)}
      </Container>
    </Layout>
  );
};

const getAirtableRows = query => {
  const rows = { ...query };
  Object.entries(rows).forEach(
    ([key, value]) => (rows[key] = value.edges.map(edge => edge.node.data))
  );
  return rows;
};

const renderNameAndList = (name, list) => (
  <>
    <p>{name}</p>
    <ul>
      {list.map((book, i) => (
        <li key={`completed-${i}`}>
          <span style={{ fontStyle: 'italic' }}>{book.Title}</span>
          <span>{book.Artist && ` by ${book.Artist}`}</span>
        </li>
      ))}
    </ul>
  </>
);

export default ShelfPage;
